<template>
<div class="aptitude">
           <el-select v-model="companyQual" placeholder="选择资质类型" clearable  @change='Splice'  >
                <el-option
                  v-for="item in companyQuals"
                  :key="item.name"
                  :label="item.name"
                  :value="item.code">
                </el-option>
              </el-select>
           
             <el-select v-model="major" placeholder="请选择" clearable  @change='spliceo'   >
              <el-option
                v-for="item in majors"
                :key="item.name"
                :label="item.name"
                :value="item.code">
              </el-option>
            </el-select>
             <el-select v-model="grade" placeholder="请选择" clearable   @change='splicet' :disabled='noLevel'  >
              <el-option
                v-for="item in grades"
                :key="item.name"
                :label="item.name"
                :value="item.code">
              </el-option>
            </el-select>
</div>
</template>
<script>
import { filter} from '@/api/index';
export default {
  data () {
    return {
      companyQual:'',
      major:'',
      grade:'',
      companyQuals:[],
      majors:[],
      grades:[],
      zType:[],
      i:0,
      noLevel:false,

    }
  },
  props: {
    index:0,
    clear:false,
    codeObj:{
      default:''
    }
  },
  methods: {
     gainFilter() {
      filter({}).then( res => {
         if(res.code == 1 ) {
            this.companyQuals = res.data.companyQual
         }
      })
    },
    Splice() {
      if(this.companyQual == '') {
        this.$emit('group', {cur:'',em:false,i:this.i})
      } else {
        this.$emit('group', {cur:'',em:true,i:this.i})
      }
    },
    spliceo() {
       this.grade = ''
       this.grades = []
        this.majors.forEach(el => {
         if(el.code == this.major ) {
            if(el.list.length == 0  ) {
              this.grade = '不分等级'
               this.noLevel = true
            } else {
               this.noLevel = false
               this.grades = el.list
            }
            
         }
      });
      if(this.noLevel) {
          this.zType = []
          this.zType.push(this.companyQual,this.major)
          this.zzType = this.zType.join('||')
          this.$emit('group', {cur:this.zzType,em:false,i:this.i})
      } else {
        this.$emit('group', {cur:'',em:true,i:this.i})
      }
        
    },
    splicet() {
      
       if(this.grade  == '') {
          this.$emit('group', {cur:'',em:true,i:this.i})
       } else {
          this.zType = []
          this.zType.push(this.companyQual,this.major,this.grade)
          this.zzType = this.zType.join('||')
          this.$emit('group', {cur:this.zzType,em:false,i:this.i})
       }
    },
  },
  watch: {
     companyQual(val) {
       if(this.codeObj!=''){
         return false
       }
      this.major = ''
      this.majors = []
      this.grade = ''
      this.grades = []
      this.noLevel = false
      this.companyQuals.forEach(el => {
         if(el.code == val ) {
            this.majors = el.list
         }
      });      
    },
    //  major(val) {
     
    
    // },
     grade(val) {
      this.zType = []
    },
    clear(val) {
      if(val) {
         this.grade = ''
      }
     
    }
  },
  created () {
    this.i = this.index
    this.gainFilter()
  },
  mounted() {
    if(this.codeObj!=''){
      let arr=this.codeObj.split('||');
      this.companyQual=arr[0];
      this.major=arr[1];
      this.grade=arr[2];
    }
  },
  components: {
  }
}
</script>
<style lang="less" scoped>
.aptitude {

}
</style>



